.wrapper_photos_profile_wall {
  background-color: rgb(51, 51, 54);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  box-shadow: none;
  color: rgb(255, 255, 255);
  display: block;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 13px;
  font-weight: 400;
  height: 190px;
  line-height: 15.002px;
  margin-top: 15px;
  margin-left: 15px;
}

.text_photos_profile_wall h1 {
  color: rgb(255, 255, 255);
  cursor: pointer;
  direction: ltr;
  display: block;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 13px;
  font-weight: 400;
  padding-right: 4px;
  max-width: 100%;
  text-overflow: ellipsis;
}
.text_photos_profile_wall span{
    color:rgb(147, 147, 147);
    cursor:pointer;
    direction:ltr;
    display:block;
    float:left;
    font-family:-apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
    font-size:13px;
    font-weight:400;
    height:40px;
    letter-spacing:normal;
    line-height:40px;
    padding-bottom:0px;
    padding-left:6px;
    padding-right:6px;
    padding-top:0px;
    text-align:left;
    width:7.3125px;
}
.text_photos_profile_wall {
  color: rgb(255, 255, 255);
  cursor: pointer;
  direction: ltr;
  display: flex;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 13px;
  font-weight: 400;
  height: 32px;
  letter-spacing: normal;
  line-height: 40px;
  margin-bottom: 0px;
  margin-right: 0px;
  margin-top: 0px;
  outline-color: rgb(255, 255, 255);
  outline-style: none;
  outline-width: 0px;
  overflow-x: hidden;
  overflow-y: hidden;
  padding-bottom: 0px;
  margin-left: 26px;
  padding-right: 20px;
  padding-top: 2px;
  text-align: left;
  width: 490px;
}
.photos_photos_profile_wall{
    display: flex;
    justify-content: center;
    margin-top: 11px;
}
.photos_photos_profile_wall img {
  background-position-x: 50%;
  background-position-y: 50%;
  padding: 0 5px;
  background-size: cover;
  color: rgb(255, 255, 255);
  cursor: pointer;
  direction: ltr;
  display: inline-block;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 13px;
  font-weight: 400;
  height: 123px;
  
  image-rendering: -webkit-optimize-contrast;
  line-height: 15.002px;
  position: relative;
  text-align: center;
  text-decoration-color: rgb(255, 255, 255);
  text-decoration-line: none;
  text-decoration-style: solid;
  width: 123px;
}
